.winner_section {
    padding-bottom: min-set-vw(15);
    &-inner {
        position: relative;
        border-radius: min-set-vw(20);
        border: 1px solid #e1e1e1;
        background-color: #fff;

        padding: min-set-vw(40) min-set-vw(27);
    }
    .tabs_list {
        display: flex;
        justify-content: center;
        gap: min-set-vw(54);
        width: 100%;
        border-bottom: min-set-vw(2) solid #b6bed1;
    }

    .tabs_item {
        position: relative;
        padding-bottom: min-set-vw(16);
        color: #686868;
        font-size: min-set-vw(27);
        letter-spacing: 1px;
        cursor: pointer;

        &::after {
            @include psudoClass;
            @include absolute($B:0, $L:0);
            width: 100%;
            height: min-set-vw(5);
            background-color: #866638;

            opacity: 0;
            transform: scaleY(0);
            transform-origin: center bottom;
            transition: transform 0.3s ease-in-out, opacity 0.3s ease-in-out;
        }
        
        &:hover, &.js-active {
            color: #866638;
            &::after {
                transform: scaleY(1);
                opacity: 1;
            }
        }
    }

    .tabs_content {

    }

    .winner_item {
        display: flex;
        justify-content: space-between;
        align-items: center;
        gap: min-set-vw(20);
        padding: min-set-vw(34) 0;
        color: #b6bed1;
        font-size: min-set-vw(24);
        &-col {
            display: flex;
            gap: min-set-vw(8);

            .icon {
                width: min-set-vw(36);

                img {
                    @include aspectRatioBox(117, 93)
                }
            }
        }

        &:not(:first-of-type) {
            border-top: min-set-vw(2) solid #b6bed1;
        }

        .time {
            white-space: nowrap;
            overflow-wrap: normal;
        }
    }

    .no-data {
        padding-top: min-set-vw(106);
        padding-bottom: min-set-vw(106);
        .tw_img {
            width: relative-percentage(424, 750);
            margin: auto;

            img {
                @include aspectRatioBox(566, 325);
            }
        }
        .txt {
            color: #b6bed1;
            font-size: min-set-vw(40);
            letter-spacing: 1px;
            text-align: center;
        }
    }
}
