.lottery_section {
    padding-top: min-set-vw(15);
    padding-bottom: min-set-vw(15);
    &-inner {
        position: relative;
        border-radius: min-set-vw(20);
        border: 1px solid #e1e1e1;
        @include aspectRatioBox(962, 1569);
        background-size: cover;

        overflow: hidden;
    }

    &-hd {
        padding-top: min-set-vw(32);
        padding-bottom: min-set-vw(28);
        margin-left: min-set-vw(38);
        margin-right: min-set-vw(38);
        border-bottom: 1px solid #e1e1e1;

        .txt {
            font-size: min-set-vw(24);
        }
        .txt-text-highlight {
            padding-left: 1px;
            padding-right: 1px;
        }
    }

    &-bd {
        .title {
            width: relative-percentage(574, 750);
            margin: auto;
            margin-top: min-set-vw(46);
        }

        .content {
            position: relative;

            .content-block {
                // position: relative;
                @include absolute(0, $L: 50%);
                z-index: -1;
                transform: translate(-50%, 0);
                opacity: 0;
            }
            .tw_img {
            }

            .scratch_card-block {
                top: min-set-vw(28);
                width: relative-percentage(644, 750);

                .scratch_card & {
                    opacity: 1;
                    z-index: 1;
                }
            }

            .smash_egg-block {
                top: min-set-vw(52);
                width: relative-percentage(684, 750);
                .smash_egg & {
                    opacity: 1;
                    z-index: 1;
                }

                .js-enterSmashEgg & {
                    cursor: none;
                }
            }

            .money_tree-block {
                top: min-set-vw(34);
                width: relative-percentage(699, 750);
                .money_tree & {
                    opacity: 1;
                    z-index: 1;
                }

                .js-enterMoneyTree & {
                    cursor: none;
                }
            }

            .img-scratch_card {
                @include aspectRatioBox(858, 1118);
            }

            .img-smash_egg {
                @include aspectRatioBox(913, 970);
            }

            .img-money_tree {
                @include aspectRatioBox(931, 1018);
                .js-enterMoneyTree & {
                    visibility: hidden;
                }
            }
        }
    }

    &-ft {
        @include absolute($B: 0);
        z-index: 2;
        width: 100%;
        padding-bottom: min-set-vw(32);
        .btns {
            display: grid;
            grid-template-columns: repeat(3, 1fr);
        }
    }
}
