.login_form {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: min-set-vw(30);
    width: relative-percentage(595, 750);
    height: 100%;
    margin: auto;

    &-field {
        width: 100%;

        .user_id-error-msg {
            &:not(:empty) {
                padding: 0.3em;

                color: var(--text-highlight2);
                font-size: min-set-vw(24);
                font-weight: bold;
            }
        }
    }
    
    &-controll {
        border-radius: min-set-vw(13);
        border: 1px solid #444;
        @include aspectRatioBox(595, 76);
        padding: 0 min-set-vw(34);

        font-size: min-set-vw(32);
    }
    &-btn {
        display: block;
        width: relative-percentage(252, 595);
        // margin: auto;
        border: none;
        background-color: transparent;

        @include element-effect(false, true);

        .tw_img {
        }
        img {
            @include aspectRatioBox(252, 86);
        }
    }
}