.money_tree {
    &-btn {
        @include absolute($L:50%);
        transform: translateX(-50%);
        top: relative-percentage(801, 1018);
        width: relative-percentage(465, 931);
        img {
            @include aspectRatioBox(465, 186);
            @include element-effect(false, true);
        }
    }

    &-hand {
        @include absolute();
        top: relative-percentage(854, 1018);
        left: relative-percentage(643, 931);

        width: relative-percentage(132, 931);
        img {
            @include aspectRatioBox(132, 164);
        }
    }
}



.money_tree-enter-group {
    @include absolute(0, $L:0);
    z-index: 2;
    width: 100%;
    @include aspectRatioBox(931, 1018);
    opacity: 0;
    pointer-events: none;

    transform-origin: 50% 100%;
    .tw_img {
        @include absolute(0, $L:0);
        width: 100%;
        img {
            @include aspectRatioBox(931, 1018);
        }

        &:not(.layer1) {
            img {
                transform: translate((relative-percentage(1.5, 931) * -1), relative-percentage(1.5, 1018));
            }
        }
    }

    &.js-active {
        opacity: 1;
        pointer-events: all;
    }

    .layer1, .layer3 {
        transform-origin: 50% 100%;
    }
}


.money_tree-cursor {
    position: absolute;
    top: calc(var(--block-y) * 1px);
    left: calc(var(--block-x) * 1px);

    width: min(relative-percentage(178, 859), 80px);
    z-index: 2000;
    pointer-events: none;

    opacity: 0;
    img {
        @include aspectRatioBox(132, 164);
    }

    .js-showMoneyTreeHand & {
        opacity: 1;
    }
    
    .js-showCursor & {
        opacity: 1;
    }
}

.money_tree-red_envelopes {
    &.layer4-1 {
        transform-origin: relative-percentage(379, 932) relative-percentage(236, 1018);
    }
    &.layer4-2 {
        transform-origin: relative-percentage(770, 932) relative-percentage(471, 1018);
    }
    &.layer4-3 {
        transform-origin: relative-percentage(215, 932) relative-percentage(462, 1018);
    }
    &.layer4-4 {
        transform-origin: relative-percentage(539, 932) relative-percentage(344, 1018);
    }
    &.layer4-5 {
        transform-origin: relative-percentage(466, 932) relative-percentage(426, 1018);
    }
    &.layer4-6 {
        transform-origin: relative-percentage(675, 932) relative-percentage(377, 1018);
    }
    &.layer4-7 {
        transform-origin: relative-percentage(328, 932) relative-percentage(377, 1018);
    }
    &.layer4-8 {
        transform-origin: relative-percentage(563, 932) relative-percentage(458, 1018);
    }
}