.info_section {
    padding-bottom: min-set-vw(15);
    &-inner {
        position: relative;
        border-radius: min-set-vw(20);
        border: 1px solid #e1e1e1;
        background-size: cover;

        padding: min-set-vw(14) min-set-vw(22);
    }

    &-hd {
        position: relative;
        .tw_img {
            @include absolute(0, $L: 50%);
            transform: translateX(-50%);
            width: relative-percentage(267, 722);
            margin: auto;

            img {
                @include aspectRatioBox(356, 145);
            }
        }
    }

    &-bd {
        &::before {
            @include psudoClass;
            @include aspectRatioBox(722, 96);
        }
        .txt {
            font-size: min-set-vw(24);
            color: #fff;
            letter-spacing: 1px;
            line-height: 1.5;
        }

        .special-txt {
            height: 63px;
            padding: min-set-vw(10) min-set-vw(18);
            border-radius: 63px;
            background-color: #4e2f20;
        }

        .d-block {
            display: block;
            padding: min-set-vw(10) 0;
        }
    }
}