.popup-bonus {
    .txt {
        @include absolute;
        left: 50%;
        transform: translate(-50%, 0);
        color: #ca1a10;
        font-size: min-set-vw(82);
        font-weight: bold;
        font-family: Arial, Helvetica, sans-serif;

        small {
            font-size: min-set-vw(60);
        }
    }

    .bonus-btn {
        @include absolute($L:50%);
        transform: translate(-50%, 0);
        background-color: transparent;
        border: none;
        .tw_img {
            @include element-effect(false, true);
        }
    }
}

@mixin bonusBuild($d) {
    .popup-block {
        @include aspectRatioBox(map-get($d, 'block-width'), map-get($d, 'block-height'));
    }

    .txt {
        top: relative-percentage(map-get($d, 'txt-top'), map-get($d, 'block-height'));
    }

    .bonus-btn {
        top: relative-percentage(map-get($d, 'btn-top'), map-get($d, 'block-height'));
        width: relative-percentage(map-get($d, 'btn-width'), map-get($d, 'block-width'));
        img {
            @include aspectRatioBox(map-get($d, 'btn-width'), map-get($d, 'btn-height'));
        }
    }
}

$scratch_card: (
    'block-width': 994,
    'block-height': 923,
    'txt-top': 453,
    'btn-top': 658,
    'btn-width': 377,
    'btn-height': 143
);
.bonus-scratch_card {
    @include bonusBuild($scratch_card);    
}


$smash_egg: (
    'block-width': 1000,
    'block-height': 1094,
    'txt-top': 668,
    'btn-top': 796,
    'btn-width': 336,
    'btn-height': 115
);

.bonus-smash_egg {
    @include bonusBuild($smash_egg);
}
$money_tree: (
    'block-width': 1000,
    'block-height': 982,
    'txt-top': 593,
    'btn-top': 717,
    'btn-width': 336,
    'btn-height': 115
);

.bonus-money_tree {
    @include bonusBuild($money_tree);
}
