.img-scratch_card {

}
.scratch_card {
    &-paper {
        @include absolute(0, $L:0);
        width: 100%;
        transform-origin: center 60%;
        img {
            @include aspectRatioBox(858, 1118)
        }
    }

    &-btn {
        @include absolute($L:50%);
        transform: translate(-50%, 0);
        top: relative-percentage(598, 1118);
        width: relative-percentage(471, 859);
        img {
            @include aspectRatioBox(354, 164);
            @include element-effect(false, true);
        }
    }
    
    &-hand {
        @include absolute();
        top: relative-percentage(663, 1118);
        left: relative-percentage(584, 859);

        width: relative-percentage(178, 859);
        img {
            @include aspectRatioBox(178, 183);
        }

        .js-enterScratchCard & {
            visibility: hidden;
        }
    }

    &-area {
        @include absolute(0, $L:50%);
        transform: translateX(-50%);
        // position: absolute;
        z-index: 999;
        // z-index: -1;
        // opacity: 0;
        top: relative-percentage(501, 1118);
        width: relative-percentage(687, 859);

        .js-enterScratchCard & {
            cursor: none;
        }
    }

    &-box {
        position: relative;
        @include aspectRatioBox(687, 336);
    }
    
    &-content {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        @include aspectRatioBox(687, 336);
        font-size: 24px;
        color: black;
        z-index: 1;
    }

    &-mask {
        position: absolute;
        z-index: 2;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }

    &-canvas {
        display: block;
        width: 100%;
        height: 100%;
    }
}


