.scratch_card-cursor {
    position: absolute;
    top: calc(var(--block-y) * 1px);
    left: calc(var(--block-x) * 1px);
    width: min(relative-percentage(178, 859), 100px);
    z-index: 2000;
    pointer-events: none;

    opacity: 0;
    img {
        @include aspectRatioBox(178, 183);
    }

    .js-showScratchCardHand & {
        opacity: 1;
    }

    .js-showCursor & {
        opacity: 1;
    }
}