.site_header {
    @include fixed(0);
    z-index: 10;
    width: clamp(320px, 100%, 750px);
    border-bottom: min-set-vw(4) solid #e1e1e1;
    background-color: #fff;
    padding-top: min-set-vw(20);
    padding-bottom: min-set-vw(20);
    .container { 
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    .header-icon {
        width: relative-percentage(96, 1000);
        img {
            @include aspectRatioBox(96, 96);
        }

        > .inner {
            display: block;
            @include element-effect(false, true);
        }
    }

    .logo {
        width: relative-percentage(351, 1000);
        img {
            @include aspectRatioBox(810, 240);
        }
    }
}