.smash_egg {
    &-btn {
        @include absolute($L:50%);
        transform: translateX(-50%);
        top: relative-percentage(514, 970);
        z-index: 10;
        width: relative-percentage(429, 913);
        img {
            @include aspectRatioBox(429, 172);
            @include element-effect(false, true);
        }

        .js-enterSmashEgg & {
            visibility: hidden;
        }
    }

    &-hammer {
        @include absolute();
        top: relative-percentage(495, 970);
        left: relative-percentage(614, 913);
        z-index: 10;

        width: relative-percentage(180, 913);
        transform-origin: 80% 80%;
        img {
            @include aspectRatioBox(180, 197);
        }

        .js-enterSmashEgg & {
            visibility: hidden;
        }
    }

    &-cracks_glow {
        @include absolute(0, $L:0);
        // z-index: 2;
        width: 100%;
        opacity: 0;

        img {   
            @include aspectRatioBox(915, 971);
        }

    }
}


.smash_egg-cracks_glow-group {
    @include absolute(0, $L:0);
    z-index: 2;
    width: 100%;
    @include aspectRatioBox(915, 971);
}
.smash_egg-cursor {
    position: absolute;
    top: calc(var(--block-y) * 1px);
    left: calc(var(--block-x) * 1px);
    width: min(relative-percentage(180, 913), 100px);
    z-index: 2000;
    transform-origin: 80% 80%;
    pointer-events: none;

    opacity: 0;
    img {
        @include aspectRatioBox(180, 197);
    }

    .js-showSmashEggHammer & {
        opacity: 1;
    }

    .js-showCursor & {
        opacity: 1;
    }
}