.popup {
    @include fixed(0, $L:50%);
    transform: translate(-50%, 0);
    z-index: -1;
    width: clamp(320px, 100%, 750px);
    height: 100vh;
    padding-left: min-set-vw(14);
    padding-right: min-set-vw(14);
    background-color: rgba(#000, 0.5);
    backdrop-filter: blur(5px);

    display: flex;
    align-items: center;
    justify-content: center;

    opacity: 0;
    transition: all 0.3s ease-in-out;

    pointer-events: none;
    
    &.popup-show {
        z-index: 9999;
        opacity: 1;
        pointer-events: auto;
        
        .popup-wrap {
            scale: 1;
            opacity: 1;
        }
    }
    
    &-wrap {
        position: relative;
        @include aspectRatioBox(960, 529);
        opacity: 0;
        scale: 0;
        transition: all 0.3s $easing3;
        // height: 100%;
    }

    &-close {
        @include absolute($R: 0);
        top: calc((min-set-vw(70) * -1) - min-set-vw(10));
        width: min-set-vw(70);

        @include element-effect(false, true);
    }

    &-block {
        background-size: cover;
        @include aspectRatioBox(960, 529);
    }
}